.main {
  padding: 3rem;
  text-align: justify;
  text-justify: inter-word;
  font-size: 1.25rem;
}

.main--resume__positions {
  padding-left: 1.25rem;
}

.position--time__stamp {
  line-height: 0.9rem;
}

.position--title {
  margin-bottom: 0;
}

.position--title a {
  text-decoration: none;
  color: var(--gray);
}

.position--description__paragraph {
  margin: 0.5rem 0;
}

.position--skills__title {
  margin: 0;
}

.position--skills_list {
  padding-left: 0;
  display: flex;
  flex-wrap: wrap;
}

.position--skills_list li {
  margin: 0 1rem;
}

@media only screen and (max-width: 750px) {
  .main {
    padding: 1rem;
    font-size: 1rem;
  }
}
